import { Component, Input, OnInit } from '@angular/core';
import { User } from '../../../@core/data/user';
import { UserService } from '../../../@core/service/user.service';

@Component({
  selector: 'verified-badge',
  templateUrl: './verified-badge.component.html',
  styleUrls: ['./verified-badge.component.css']
})
export class VerifiedBadgeComponent implements OnInit {

  @Input() value: User;

  constructor(private userService: UserService) { }

  ngOnInit(): void {
  }

  getCss() {
    let css = "badge ";
    if (this.value.verified && this.value.verificationToken === '')
      return css + "bg-success";
    else if (this.value.verified || this.value.verificationToken === '')
      return css + "bg-warning";
    else
      return css + "bg-danger";
  }

  acceptUser() {
    this.value.verified = true;
    this.userService.updateUser(this.value, this.value._id).subscribe(user => {
      this.value = user;
    })
  }

}
